<template>
	<div>
		<div class="top-content">
			<div class="top-content-left" v-if="questionList.length > 0"> <el-tag type="danger">{{curQuesIndex + 1}}</el-tag> <el-tag type="danger">{{questionList.length - curQuesIndex - 1}}</el-tag> <el-tag type="danger">{{questionList.length}}</el-tag> </div>
			<div class="top-content-left" v-else><el-tag type="danger">0</el-tag> <el-tag type="danger">0</el-tag> </div>
			<div class="top-content-center" v-cloak> <el-tag type="danger">{{this.hour}}</el-tag>  <el-tag type="danger">{{this.minute}}</el-tag>  <el-tag type="danger">{{this.second}}</el-tag> </div>
			<div class="top-content-right">
				<el-button type="danger" @click="doneExam(0)"></el-button>
			</div>
		</div>
		<div class="question-content" v-for="(item, index) in questionList" :key="item.id" v-if="curQuesIndex == index">
			<div class="question-title" v-if="!end">
				 ({{item.score}})<span class="ql-snow ql-editor" v-html="item.questionname"></span>
				<el-tag type="success" v-if="item.type==0">{{questionType(item)}}</el-tag>
				<el-tag type="warning" v-if="item.type==1">{{questionType(item)}}</el-tag>
				<el-tag type="info" v-if="item.type==2">{{questionType(item)}}</el-tag>
				<el-tag type="primary" v-if="item.type==3">{{questionType(item)}}</el-tag>
				<el-tag type="danger" v-if="item.type==4">{{questionType(item)}}</el-tag>
			</div>
			<div class="question-answer">
				<div v-if="!isSubmit && !end">
					
					<el-select v-model="examrecord.myanswer" placeholder="" v-if="item.type == 0 || item.type == 2">
						<el-option
							v-for="(option, idx) in JSON.parse(item.options)"
							:key="idx"
							:label="option.text"
							:value="option.code">
						</el-option>
					</el-select>
					<el-select v-model="myanswer" multiple placeholder="" v-if="item.type == 1">
						<el-option
							v-for="(option, idx) in JSON.parse(item.options)"
							:key="idx"
							:label="option.text"
							:value="option.code">
						</el-option>
					</el-select>
					<el-input style="width: 400px;" v-if="item.type == 3" v-model="examrecord.myanswer" placeholder=""></el-input>
					<el-input style="width: 400px;" v-if="item.type == 4" type="textarea" v-model="examrecord.myanswer" placeholder=""></el-input>
				</div>
				<div v-if="isSubmit && !end">
					<div class="question-title"> {{question.type == 1 ? myanswer.join(',') : examrecord.myanswer}}</div>
					<div v-if="question.answer == examrecord.myanswer&&question.type!=4" class="question-title"><el-tag type="success"></el-tag> {{question.answer}}, {{question.analysis}}</div>
					<div v-if="question.answer != examrecord.myanswer&&question.type!=4" class="question-title"><el-tag type="danger"></el-tag> {{question.answer}}, {{question.analysis}}</div>
				</div>
				<div v-if="end">
					<div class="question-title"><el-tag type="danger">{{totalScore}}</el-tag> </div>
				</div>
			</div>
			<div class="question-btn">
				<el-button v-if="!isSubmit && !end" type="primary" @click="submitAnswer"></el-button>
				<el-button v-if="isSubmit && !end" type="success" @click="nextQuestion"></el-button>
				<el-button v-if="end" type="success" @click="doneExam(1)"></el-button>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
    //
		data() {
			return {
				questionList: [],
				curQuesIndex: 0,
				question: {},
				examrecord: {
					analysis: '',
					answer: '',
					myanswer: '',
					myscore: '',
					options: '',
					paperid: '',
					papername: '',
					questionid: '',
					questionname: '',
					score: '',
					userid: Number(localStorage.getItem('frontUserid')),
					username: localStorage.getItem('username')
				},
				myanswer: [],
				totalScore: 0,
				isSubmit: false,
				end: false,
				maxtime: 0,
				timer: null,
				hour: '',
				minute: '',
				second: '',
				user: {},
				hassubject:false,
				paper:{},
			}
		},
		created() {
			this.$http.get(`exampaper/info/${this.$route.query.id}`, {}).then(res => {
				if (res.data.code == 0) {
					this.paper = res.data.data
					this.maxtime = this.paper.time * 60;
					this.deleteRecords(this.paper.id);
					this.getQuestion(this.paper.id);
					this.hour = Math.floor(this.maxtime / 60 / 60);
					this.minute = Math.floor(this.maxtime / 60 % 60);
					this.second = Math.floor(this.maxtime % 60);
					this.timer = setInterval(this.countDown, 1000);
				}
			})
			this.$http.get(localStorage.getItem('UserTableName') + '/session', {
				emulateJSON: true
			}).then(res => {
				if (res.data.code == 0) {
					this.user = res.data.data;
				}
			});
		},
		//
		methods: {
			questionType(item) {
				switch (item.type) {
					case 0:
						return '';
					case 1:
						return '';
					case 2:
						return '';
					case 3:
						return '';
					case 4:
						return '';
				}
			},
			nextQuestion() {
				this.isSubmit = false;
				this.examrecord.myanswer = '';
				if (this.curQuesIndex >= this.questionList.length -1) {
					this.end = true;
					return;
				}
				this.curQuesIndex++;
				this.question = this.questionList[this.curQuesIndex];
			},
			deleteRecords(paperid) {
				this.$http.post(`examrecord/deleteRecords?userid=${localStorage.getItem('frontUserid')}&paperid=${paperid}`).then(res => {});
			},
			getQuestion(paperid) {
				this.$http.get('examquestion/list', {params: {page: 1, limit: 1000, paperid}}).then(res => {
					if (res.data.code == 0) {
						for(let x in res.data.data.list){
							res.data.data.list[x].questionname = res.data.data.list[x].questionname.replace(/img src/gi,"img style=\"width:100%;\" src");
							if(res.data.data.list[x].type==4){
								this.hassubject = true
							}
						}
						res.data.data.list.sort(function (a, b) {
							return (b.sequence - a.sequence)
						});
						this.questionList = res.data.data.list;
						if (res.data.data.list.length > 0) {
							this.question = res.data.data.list[0];
						}
					}
				});
			},
			submitAnswer() {
				this.examrecord.myanswer = this.question.type == 1 ? this.myanswer.sort().join(',') : this.examrecord.myanswer;
				this.examrecord.analysis = this.question.analysis;
				this.examrecord.answer = this.question.answer;
				this.examrecord.myscore = this.question.type!=4?(this.examrecord.myanswer == this.question.answer ? this.question.score : 0):0
				this.examrecord.options = this.question.options;
				this.examrecord.type = this.question.type;
				this.examrecord.ismark = this.hassubject? 0 : 1
				this.examrecord.username = this.user.yonghuxingming;
				this.examrecord.paperid = this.question.paperid;
				this.examrecord.papername = this.question.papername;
				this.examrecord.questionid = this.question.id;
				this.examrecord.questionname = this.question.questionname;
				this.examrecord.score = this.question.score;
				this.totalScore = parseFloat(this.totalScore) + parseFloat(this.examrecord.myscore);
				this.$http.post('examrecord/add', this.examrecord).then(res => {
					if (res.data.code == 0 && this.questionList.length > this.curQuesIndex) {
						this.isSubmit = true;
					} else {
						this.$message.error(res.data.msg);
					}
				});
			},
			doneExam(type) {
				if (type == 0) {
					this.$confirm('?0', '', {
						confirmButtonText: '',
						cancelButtonText: '',
						type: 'warning'
					}).then(() => {
						this.end = true;
						if (this.questionList.length == 0) {
							this.$router.push('/index/examPaper');
						}
					});
				} else {
					this.$router.push('/index/examPaper');
				}
			},
			countDown() {
				if (this.maxtime > 0) {
					this.hour = Math.floor((this.maxtime - 1) / 60 / 60);
					this.minute = Math.floor((this.maxtime - 1) / 60 % 60);
					this.second = Math.floor((this.maxtime - 1) % 60);
					--this.maxtime;
				} else {
					clearInterval(this.timer);
					this.end = true;
				}
			}
		}
	}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
	.top-content {
		height: 80px;
		width: 100%;
		background-color: #409EFF;
		display: flex;
		justify-content: space-between;
	}
	.top-content-left, .top-content-center, .top-content-right {
		font-size: 18px;
		line-height: 80px;
		color: #fff;
	}
	.top-content-left {
		padding-left: 20px;
	}
	.top-content-right {
		padding-right: 20px;
	}
	.question-content {
		width: 90%;
		margin: 0 auto;
		margin-top: 30px;
	}
	.question-title, .question-answer, .question-btn {
		padding: 20px 0;
	}
</style>
