<template>
	<div class="add-update-preview">
		<el-form
			class="add-update-form"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="180px"
			>
			<el-form-item class="add-item" label="" prop="ziyuanmingcheng">
				<el-input v-model="ruleForm.ziyuanmingcheng" 
					placeholder="" clearable :disabled=" false  ||ro.ziyuanmingcheng"></el-input>
			</el-form-item>
			<el-form-item class="add-item"  label="" prop="ziyuanfenlei">
				<el-select v-model="ruleForm.ziyuanfenlei" placeholder="" :disabled=" false  ||ro.ziyuanfenlei" >
					<el-option
						v-for="(item,index) in ziyuanfenleiOptions"
						:key="index"
						:label="item"
						:value="item">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item class="add-item" label="" v-if="type!='cross' || (type=='cross' && !ro.fengmian)" prop="fengmian">
				<file-upload
					tip=""
					action="file/upload"
					:limit="3"
					:multiple="true"
					:fileUrls="ruleForm.fengmian?ruleForm.fengmian:''"
					@change="fengmianUploadChange"
					></file-upload>
			</el-form-item>
			<el-form-item class="add-item" v-else label="" prop="fengmian">
				<img v-if="ruleForm.fengmian.substring(0,4)=='http'" class="upload-img" v-bind:key="index" :src="ruleForm.fengmian.split(',')[0]">
				<img v-else class="upload-img" v-bind:key="index" v-for="(item,index) in ruleForm.fengmian.split(',')" :src="baseUrl+item">
			</el-form-item>
			<el-form-item class="add-item" label="" prop="ziyuanwenjian">
				<file-upload
					tip=""
					action="file/upload"
					:limit="1"
					:type="3"
					:multiple="true"
					:fileUrls="ruleForm.ziyuanwenjian?ruleForm.ziyuanwenjian:''"
					@change="ziyuanwenjianUploadChange"
					></file-upload>
			</el-form-item>  
			<el-form-item class="add-item" label="" v-if="type!='cross' || (type=='cross' && !ro.ziyuanshipin)" prop="ziyuanshipin">
				<file-upload
					tip=""
					action="file/upload"
					:limit="1"
					:type="2"
					:multiple="true"
					:fileUrls="ruleForm.ziyuanshipin?ruleForm.ziyuanshipin:''"
					@change="ziyuanshipinUploadChange"
					></file-upload>
			</el-form-item>
			<el-form-item class="add-item" v-else label="" prop="ziyuanshipin">
				<div class="viewBtn" v-if="ruleForm.ziyuanshipin" @click="download(baseUrl + ruleForm.ziyuanshipin)" type="success"></div>
				<div class="unviewBtn" v-else disabled></div>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="fabushijian">
				<el-date-picker
					:disabled=" false  ||ro.fabushijian"
					value-format="yyyy-MM-dd HH:mm:ss"
					v-model="ruleForm.fabushijian" 
					type="datetime"
					placeholder="">
				</el-date-picker>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="ziyuanxiangqing">
				<editor 
					v-model="ruleForm.ziyuanxiangqing" 
					class="editor" 
					myQuillEditor="ziyuanxiangqing"
					action="file/upload">
				</editor>
			</el-form-item>

			<el-form-item class="add-btn-item">
				<el-button class="submitBtn"  type="primary" @click="onSubmit">
					<span class="icon iconfont "></span>
					<span class="text"></span>
				</el-button>
				<el-button class="closeBtn" @click="back()">
					<span class="icon iconfont "></span>
					<span class="text"></span>
				</el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				id: '',
				baseUrl: '',
				ro:{
					ziyuanmingcheng : false,
					ziyuanfenlei : false,
					fengmian : false,
					ziyuanwenjian : false,
					ziyuanshipin : false,
					ziyuanxiangqing : false,
					fabushijian : false,
					thumbsupnum : false,
					crazilynum : false,
					clicktime : false,
					clicknum : false,
					discussnum : false,
					storeupnum : false,
				},
				type: '',
				userTableName: localStorage.getItem('UserTableName'),
				ruleForm: {
					ziyuanmingcheng: '',
					ziyuanfenlei: '',
					fengmian: '',
					ziyuanwenjian: '',
					ziyuanshipin: '',
					ziyuanxiangqing: '',
					fabushijian: '',
					thumbsupnum: '',
					crazilynum: '',
					clicktime: '',
					clicknum: '',
					discussnum: '',
					storeupnum: '',
				},
				ziyuanfenleiOptions: [],


				rules: {
					ziyuanmingcheng: [
					],
					ziyuanfenlei: [
					],
					fengmian: [
					],
					ziyuanwenjian: [
					],
					ziyuanshipin: [
					],
					ziyuanxiangqing: [
					],
					fabushijian: [
					],
					thumbsupnum: [
						{ validator: this.$validate.isIntNumer, trigger: 'blur' },
					],
					crazilynum: [
						{ validator: this.$validate.isIntNumer, trigger: 'blur' },
					],
					clicktime: [
					],
					clicknum: [
						{ validator: this.$validate.isIntNumer, trigger: 'blur' },
					],
					discussnum: [
						{ validator: this.$validate.isIntNumer, trigger: 'blur' },
					],
					storeupnum: [
						{ validator: this.$validate.isIntNumer, trigger: 'blur' },
					],
				},
				centerType: false,
			};
		},
		computed: {



		},
		components: {
		},
		created() {
			if(this.$route.query.centerType){
				this.centerType = true
			}
			//this.bg();
			let type = this.$route.query.type ? this.$route.query.type : '';
			this.init(type);
			this.baseUrl = this.$config.baseUrl;
			this.ruleForm.fabushijian = this.getCurDateTime()
		},
		methods: {
			getMakeZero(s) {
				return s < 10 ? '0' + s : s;
			},
			// 
			download(file){
				window.open(`${file}`)
			},
			// 
			init(type) {
				this.type = type;
				if(type=='cross'){
					var obj = JSON.parse(localStorage.getItem('crossObj'));
					for (var o in obj){
						if(o=='ziyuanmingcheng'){
							this.ruleForm.ziyuanmingcheng = obj[o];
							this.ro.ziyuanmingcheng = true;
							continue;
						}
						if(o=='ziyuanfenlei'){
							this.ruleForm.ziyuanfenlei = obj[o];
							this.ro.ziyuanfenlei = true;
							continue;
						}
						if(o=='fengmian'){
							this.ruleForm.fengmian = obj[o]?obj[o].split(",")[0]:'';
							this.ro.fengmian = true;
							continue;
						}
						if(o=='ziyuanwenjian'){
							this.ruleForm.ziyuanwenjian = obj[o];
							this.ro.ziyuanwenjian = true;
							continue;
						}
						if(o=='ziyuanshipin'){
							this.ruleForm.ziyuanshipin = obj[o];
							this.ro.ziyuanshipin = true;
							continue;
						}
						if(o=='ziyuanxiangqing'){
							this.ruleForm.ziyuanxiangqing = obj[o];
							this.ro.ziyuanxiangqing = true;
							continue;
						}
						if(o=='fabushijian'){
							this.ruleForm.fabushijian = obj[o];
							this.ro.fabushijian = true;
							continue;
						}
						if(o=='thumbsupnum'){
							this.ruleForm.thumbsupnum = obj[o];
							this.ro.thumbsupnum = true;
							continue;
						}
						if(o=='crazilynum'){
							this.ruleForm.crazilynum = obj[o];
							this.ro.crazilynum = true;
							continue;
						}
						if(o=='clicktime'){
							this.ruleForm.clicktime = obj[o];
							this.ro.clicktime = true;
							continue;
						}
						if(o=='clicknum'){
							this.ruleForm.clicknum = obj[o];
							this.ro.clicknum = true;
							continue;
						}
						if(o=='discussnum'){
							this.ruleForm.discussnum = obj[o];
							this.ro.discussnum = true;
							continue;
						}
						if(o=='storeupnum'){
							this.ruleForm.storeupnum = obj[o];
							this.ro.storeupnum = true;
							continue;
						}
					}
				}else if(type=='edit'){
					this.info()
				}
				// 
				this.$http.get(this.userTableName + '/session', {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						var json = res.data.data;
					}
				});
				this.$http.get('option/ziyuanfenlei/ziyuanfenlei', {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						this.ziyuanfenleiOptions = res.data.data;
					}
				});

				if (localStorage.getItem('raffleType') && localStorage.getItem('raffleType') != null) {
					localStorage.removeItem('raffleType')
					setTimeout(() => {
						this.onSubmit()
					}, 300)
				}
			},

			// 
			// 
			info() {
				this.$http.get(`xuexiziyuan/detail/${this.$route.query.id}`, {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						this.ruleForm = res.data.data;
					}
				});
			},
			// 
			async onSubmit() {
				if(!this.ruleForm.id) {
					delete this.ruleForm.userid
				}
				await this.$refs["ruleForm"].validate(async valid => {
					if(valid) {
						if(this.type=='cross'){
							var statusColumnName = localStorage.getItem('statusColumnName');
							var statusColumnValue = localStorage.getItem('statusColumnValue');
							if(statusColumnName && statusColumnName!='') {
								var obj = JSON.parse(localStorage.getItem('crossObj'));
								if(!statusColumnName.startsWith("[")) {
									for (var o in obj){
										if(o==statusColumnName){
											obj[o] = statusColumnValue;
										}
									}
									var table = localStorage.getItem('crossTable');
									await this.$http.post(table+'/update', obj).then(res => {});
								}
							}
						}


						await this.$http.post(`xuexiziyuan/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(async res => {
							if (res.data.code == 0) {
								this.$message({
									message: '',
									type: 'success',
									duration: 1500,
									onClose: () => {
										this.$router.go(-1);
										
									}
								});
							} else {
								this.$message({
									message: res.data.msg,
									type: 'error',
									duration: 1500
								});
							}
						});
					}
				});
			},
			// uuid
			getUUID () {
				return new Date().getTime();
			},
			// 
			back() {
				this.$router.go(-1);
			},
			fengmianUploadChange(fileUrls) {
				this.ruleForm.fengmian = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
			},
			ziyuanwenjianUploadChange(fileUrls) {
				this.ruleForm.ziyuanwenjian = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
			},
			ziyuanshipinUploadChange(fileUrls) {
				this.ruleForm.ziyuanshipin = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
			},
		}
	};
</script>

